* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  background-color: beige;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding: 20px;
}

.App {
  background-color: aqua;
  max-width: 800px;
  border: 2px solid black;
  border-radius: 10px;
  margin: 100px auto;
}

h1, h2 {
  text-align: center;
  font-style: italic;
}

h1:hover {
  opacity: 0.7;
}

.mainFrame {
  
  width: 95%;
  
  padding: 20px;
  
  margin: 0 auto;
  text-align: center;
}

button {
  background-color: transparent;
  cursor: pointer;
  border-radius: 10px;
}

input, button {
  height: 40px;
  margin-right: 5px;
  font-size: 15px;
  padding: 5px;
}


img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mainPanel {
  display: flex;
  justify-content: center;
}

.buttons, .input {
  display: flex;
  justify-content: center;
  align-items: center;
}

hr {
  margin: 10px;
  border: 2px dashed black
}

span.decorated {
  text-decoration: line-through;;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.removeButton {
  margin: 5px 20px;
}

.inputSpace {
  border: none;
  border-bottom: 1px solid black;
  border-radius: 2px;
  
}

.inputSpace:focus {
  outline: none;
}

.addTask {
  
  padding: 5px;
}

button:hover {
  background-color: rgba(0, 201, 100, 0.7)
}

